#App {
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    min-width: 320px;
}

header {
    background-color: #282c34;
    font-size: 30px;
    color: white;
    padding: 7.5px 0;
    flex: 0 0 auto;
}

#Info {
    background-color: beige;
    color: black;
    font-size: 24px;
    padding: 6px 0;
    flex: 0 0 auto;
}

#Info .score {
    display: inline-block;
    margin: 0 10px;
}

#StartPage {
    flex: 1 1 auto;
}

#StartPage label {
    margin: 0 10px 0 0;
}

#main-container {
    display: flex;
    font-size: 18px;
    flex: 1 1 auto;
    min-height: 0;
}

#StartPage,
#EndPage {
    font-size: 18px;
}

#game,
#EndPage {
    flex: 0 0 30%;
    min-width: 200px;
}

#Result ul li.player0::marker {
    color: green;
}

#Result ul li.player1::marker {
    color: red;
}

#Result ul li.player2::marker {
    color: blue;
}

#Result ul li.player3::marker {
    color: orange;
}

#Map {
    flex: 0 1 70%;
    height: auto;
}

#game,
#EndPage {
    /* add scrollbar within container if overflows
       auto works for Firefox and overlay works for Safari/Chrome */
    overflow-y: auto;
    overflow-y: overlay;
}

#Result ul {
    display: inline-block;
    font-size: 18px;
}

footer {
    background-color: #282c34;
    padding: 5px 10px;
    text-align: left;
    flex: 0 0 auto;
}

footer a {
    color: white;
    font-size: 16px;
}
